'use client';
import React from 'react';
import { useQuery } from '@tanstack/react-query';
import { getClients, deleteClient } from '@/services/clients';
import { DataGrid, GridColDef } from '@mui/x-data-grid';
import { Button, Stack } from '@mui/material';

export function ClientsList() {
  const { data = [], refetch } = useQuery({ queryKey: ['clients'], queryFn: getClients });
  const cols: GridColDef[] = [
    { field: 'email', headerName: 'Email', flex: 1 },
    { field: 'firstName', headerName: 'First', width: 140 },
    { field: 'lastName', headerName: 'Last', width: 140 },
    { field: 'sport', headerName: 'Sport', width: 120 },
    { field: 'actions', headerName: 'Actions', width: 240, renderCell: (p) => (
      <Stack direction="row" spacing={1}>
        <Button size="small" href={`/clients/${p.row.id}`}>View</Button>
        <Button size="small" href={`/clients/${p.row.id}/edit`}>Edit</Button>
        <Button size="small" color="error" onClick={async ()=>{ await deleteClient(p.row.id); refetch(); }}>Delete</Button>
      </Stack>
    ) }
  ];
  return <div style={{ height: 520 }}><DataGrid rows={data as any[]} columns={cols} getRowId={(r)=>r.id} /></div>;
}


